export const rating = [1, 2, 3, 4, 5];

export enum statusVariant {
  Canceled = "canceled",
  Confirmed = "confirmed",
  Executed = "executed",
}

export const Validator = {
  name: {
    value: /^[-]+$/,
    message: " ,  ",
  },
  minLength(num) {
    return {
      value: num,
      message: `   - ${num}`,
    };
  },
  maxLength(num) {
    return {
      value: num,
      message: `   - ${num}`,
    };
  },
  password: {
    value: /^[A-z0-9]+$/,
    message: "   ",
  },
  email: {
    value: /^([a-z0-9_-]+.)*[a-z0-9_-]+@[a-z0-9_-]+(.[a-z0-9_-]+)*.[a-z]{2,6}$/,
    message: " ",
  },
};
